import React from 'react';
// import Navbar from './navbar/navbar';
import UserPc from './images/download.png'
import Paper from '@material-ui/core/Paper';
import firebase from './../firebase'
class Settings extends React.Component {

  constructor() {
    super()
    this.state = {

    }
  }
  componentDidMount() {
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.setState({ user });
      }
    });
  }

  logout = () => {
    firebase.auth().signOut().then(() => {
      this.setState({
        user: null
      });
    });
  }

  render() {
    console.log(this.props)
    return (

      <Paper style={{
        width: "80%", marginTop: 0,
        marginRight: 'auto',
        marginBottom: 0,
        borderRadius: '10px',
        marginLeft: 'auto',
        backgroundColor: '#f7f7f7'
      }}>
        <center>
          <h3 style={{ fontFamily: 'Montserrat', paddingTop: '20px' }}>Profile</h3>
          <div>
            {/* <Navbar/> */}
            <div style={{ marginTop: '30px' }}>
              <img src="data:image/png;base64," width="100px" height="100px" />
            </div>
            <div >
              {this.state.user &&
                <div className="App-header-login" >
                  <h1 style={{ fontFamily: 'Montserrat' }}>{this.state.user.displayName}</h1>
                  <h1 style={{ marginRight: '230px', cursor: 'pointer', marginTop: '100px', borderBottom: '1px solid' }}>Email </h1>
                  <h1 style={{ marginRight: '210px', borderBottom: '1px solid', cursor: 'pointer' }}>Setting </h1>
                  <h1 style={{ marginRight: '210px', cursor: 'pointer', borderBottom: '1px solid' }} onClick={this.logout} >Logout</h1>

                </div>
              }
            </div>
            <div>

            </div>
          </div>

        </center>
        <br />
        <br />
      </Paper>
    );
  };
}
export default Settings;